import React from "react";
import "./index.css";
import EnyCloud  from '../assets/ic_EnyCloud.png'
const WordCloud = ({ words }) => {
  return (
    <div className="word-cloud">
      <p className='title'>
     
        <img src={EnyCloud} alt=''></img>
        
      </p>
      {words.map((word, index) => (
        <span
          key={index}
          className="word"
          style={{
            top: word.top, // 
            left: word.left, // 
            fontSize: word.fontSize, // 
            color: word.color, // 
            animation: `float ${Math.random() * 3 + 2}s ease-in-out infinite ${index * 0.5}s`, // 
          
          }}
        >
          {word.text}
        </span>
      ))}
    </div>
  );
};

export default WordCloud;
